	export default {
		data() {
			return {
				id_number: "",
				verify_failed: ''
			};
		},
		methods: {
			go_forget() {
				uni.navigateTo({
					url: '../../pages/ucenter/forget'
				});
			},
			go_register() {
				uni.navigateTo({
					url: '../../pages/ucenter/register'
				});
			},
			goToMyInfo() {
				uni.switchTab({
					url: 'my_info'
				});
			},
			verifyId() {
				console.log(this.id_number);
				uni.setStorage({
					key: 'key_id_number',
					data: this.id_number
				});

				uni.request({
					url: 'http://114.116.64.103:9000/student_login_api',
					method: "POST",
					dataType: 'json',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data: {
						stu_num_id: this.id_number
					},
					success: function(result) {
                        if (result.data.error === 0) {
                            uni.switchTab({
                                url: 'my_info'
                            });
                        }
                        else {
                            this.verify_failed = '验证未通过，请重新验证';
                        }
					}.bind(this)
				});
			}
		}
	};
